
















































































































































































import { Vue, Component } from 'vue-property-decorator';
import { HomeData } from '@/types/views/home.interface';
import BaseImg from '@/components/BaseImg.vue';
import BaseAvatar from '@/components/BaseAvatar.vue';

@Component({
  components: {
    BaseImg,
    BaseAvatar
  },

  data(): HomeData {
    return {
      carouselItems: [], // 轮播图
      tabItems: [], // 首推选项卡
      activeTab: 0, // 当前选中的选项卡
      tabLoading: false // 选项卡内容加载中
    };
  }
})
export default class Home extends Vue {
  carouselItems!: object[]; // 轮播图
  tabItems!: object[]; // 首推选项卡
  activeTab!: number; // 当前选中的选项卡
  tabLoading!: boolean; // 选项卡内容加载中

  // 轮播图高度
  get carouselHeight() {
    if (this.$vuetify.breakpoint.xs) {
      return 160;
    } else if (this.$vuetify.breakpoint.sm) {
      return 280;
    } else if (this.$vuetify.breakpoint.md) {
      return 400;
    } else if (this.$vuetify.breakpoint.lg) {
      return 480;
    }
    return 560;
  }

  // 首推选项卡高度
  get tabsHeight() {
    if (this.$vuetify.breakpoint.md) {
      return 56;
    } else if (this.$vuetify.breakpoint.lgAndUp) {
      return 64;
    }
    return '';
  }

  // 路由跳转相关
  goToPage(to: any) {
    if (to.name) {
      // 如果跳转路由和当前路由一致，则不跳转
      if (this.$route.name === to.name) {
        return;
      }
      this.$router.push({
        name: to.name
      });
    } else if (to.path) {
      // 如果跳转路由和当前路由一致，则不跳转
      if (this.$route.path === to.path) {
        return;
      }
      this.$router.push({
        path: to.path
      });
    }
  }

  // 获取轮播图后台数据
  fetchCarouselItemsData() {
    // this.$api.userApi.home.getCarouselItems().then(
    //   (response: any) => {
    //     // console.log(response.data);
    //     this.carouselItems = response.data;
    //   },
    //   (error: any) => {
    //     console.log(`${error.response.status}: ${error.response.statusText}`);
    //   }
    // );

    this.carouselItems = this.$api.userApi.home.getCarouselItems().data;
  }

  // 获取首推选项卡后台数据
  fetchTabItemsData() {
    // this.$api.userApi.home.getTabItems().then(
    //   (response: any) => {
    //     // console.log(response.data);
    //     this.tabItems = response.data;
    //   },
    //   (error: any) => {
    //     console.log(`${error.response.status}: ${error.response.statusText}`);
    //   }
    // );

    this.tabItems = this.$api.userApi.home.getTabItems().data;
    // this.tabLoading = true;
    // setTimeout(() => {
    //   this.tabLoading = false;
    //   this.tabItems = this.$api.userApi.home.getTabItems().data;
    // }, 100);
  }

  // 生命周期钩子函数
  created() {
    this.fetchCarouselItemsData();
    this.fetchTabItemsData();
  }
}
