






























































import { Vue, Component, Prop } from 'vue-property-decorator';
// import { BaseAvatarData } from '@/types/components/baseAvatar.interface';
import BaseImg from '@/components/BaseImg.vue';

@Component({
  components: {
    BaseImg
  }

  // data(): BaseAvatarData {
  //   return {
  //     msg: 'BaseAvatar'
  //   };
  // }
})
export default class BaseAvatar extends Vue {
  // msg!: string;

  @Prop({ default: 'borderColor4' }) private color!: string; // 头像颜色
  @Prop() private tile!: boolean; // 删除组件的 border-radius。

  @Prop() private left!: boolean; // 指定头像在组件左侧。它被诸如 v-chip 和 v-btn 等组件绑在一起。
  @Prop() private right!: boolean; // 指定头像在组件的右侧。它被诸如 v-chip 和 v-btn 等组件绑在一起。

  @Prop({ default: 48 }) private size!: number | string; // 设置组件的高度和宽度。
  @Prop() private width!: number | string; // 设定组件的宽度。
  @Prop() private height!: number | string; // 设定组件的高度。
  @Prop() private minWidth!: number | string; // 设定组件的最小宽度。
  @Prop() private maxWidth!: number | string; // 设定组件的最大宽度。
  @Prop() private minHeight!: number | string; // 设定组件的最小高度。
  @Prop() private maxHeight!: number | string; // 设定组件的最大高度。

  // 图片头像展示类型
  @Prop() private src!: string | object; // 图像的 URL。这个属性是强制性的
  @Prop({ default: require('@/assets/svgs/default-account.svg') }) private lazySrc!: string; // 在等待主映像加载时显示的内容，通常是一个小型的base64编码的缩略图。有一个轻微的模糊滤镜应用。使用 vuetify-loader 来自动生成
  @Prop({ default: require('@/assets/svgs/default-account-split.svg') })
  private loadingFailedImg!: string; // 加载失败图

  @Prop({ default: 'fontColor' }) private loadingColor!: string; // 加载环的颜色
  // @Prop({ default: 20 }) private loadingSize!: number | string; // 加载环的大小
  // @Prop({ default: 2 }) private loadingWidth!: number | string; // 加载环的线宽

  @Prop({ default: 'white' }) private loadingIconColor!: string; // 加载图标的颜色
  // @Prop({ default: 32 }) private loadingIconSize!: number | string; // 加载图标的大小
  @Prop({ default: 'mdi-account' })
  private loadingIconClass!: string; // 加载图标的名称

  @Prop({ default: false }) private loadingFailed!: boolean; // 加载失败flag
  @Prop({ default: 'white' }) private loadingFailedIconColor!: string; // 加载失败图标的颜色
  // @Prop({ default: 32 }) private loadingFailedIconSize!: number | string; // 加载失败图标的大小
  @Prop({ default: 'mdi-account' })
  private loadingFailedIconClass!: string; // 加载失败图标的名称

  // 图标头像展示类型
  @Prop() private icon!: string; // 图标名称
  @Prop({ default: 'white' }) private iconColor!: string; // 图标颜色
  @Prop() private iconDark!: boolean; // 暗色主题
  @Prop() private iconLight!: boolean; // 浅色主题

  // 字符头像展示类型
  @Prop() private text!: string; // 字符名称
  @Prop({ default: 'white--text' }) private textClass!: string; // 字符需要应用的类

  // 图片头像展示类型 - 加载环的大小
  get loadingSize() {
    return Number(this.size) * 0.6; // 0.65
  }

  // 图片头像展示类型 - 加载环的线宽
  get loadingWidth() {
    return Number(this.size) * 0.05;
  }

  // 图片头像展示类型 - 加载图标的大小
  get loadingIconSize() {
    return Number(this.size) * 0.6; // 0.75
  }

  // 图片头像展示类型 - 加载失败图标的大小
  get loadingFailedIconSize() {
    return Number(this.size) * 0.6; // 0.75
  }

  // 图标头像展示类型 - 图标大小
  get iconSize() {
    return Number(this.size) * 0.6; // 0.75
  }

  // 字符头像展示类型 - 字符大小
  get textSize() {
    return `${Number(this.size) * 0.38}px`;
  }
}
