






























































import { Vue, Component, Prop } from 'vue-property-decorator';
// import { BaseImgData } from '@/types/components/baseImg.interface';

@Component({
  // data(): BaseImgData {
  //   return {
  //     msg: 'BaseImg'
  //   };
  // }
})
export default class BaseImg extends Vue {
  // msg!: string;

  @Prop() private alt!: string; // 屏幕阅读器的备用文本。 留空以装饰图像
  @Prop() private eager!: boolean; // 将强制组件内容在加载时呈现。如果存在内容的话，则不会在 DOM 中渲染，如果你想优化 SEO，这是非常有用的。

  @Prop() private src!: string | object; // 图像的 URL。这个属性是强制性的
  @Prop() private lazySrc!: string; // 在等待主映像加载时显示的内容，通常是一个小型的base64编码的缩略图。有一个轻微的模糊滤镜应用。使用 vuetify-loader 来自动生成
  @Prop() private gradient!: string; // 将渐变叠加到图像上。 仅支持 linear-gradient 语法，其他任何操作都应使用类完成（请参见示例）
  @Prop() private aspectRatio!: string | number; // 计算为width/height，因此对于 1920x1080px 的图片，其值为 1.7778。 如果省略，将自动计算
  @Prop() private contain!: boolean; // 防止图像不合适时被裁剪
  @Prop() private position!: string; // 覆盖默认设置，以更改被裁剪的部分。 使用与以下相同的语法 background-position

  @Prop() private width!: number | string; // 设定组件的宽度。
  @Prop() private height!: number | string; // 设定组件的高度。
  @Prop() private minWidth!: number | string; // 设定组件的最小宽度。
  @Prop() private maxWidth!: number | string; // 设定组件的最大宽度。
  @Prop() private minHeight!: number | string; // 设定组件的最小高度。
  @Prop() private maxHeight!: number | string; // 设定组件的最大高度。

  @Prop() private options!: object; //
  @Prop() private sizes!: string; // 使用 srcset，请参见 MDN
  @Prop() private srcset!: string; // 根据设备大小使用的一组备用图像。
  @Prop() private transition!: boolean | string; // 从 lazy-src 切换到 src 时要使用的过渡

  @Prop() private bgColor!: string; // 图像的背景色

  // @Prop({ default: true }) private loading!: boolean; // 加载中flag
  @Prop({ default: 'fontColor' }) private loadingColor!: string; // 加载环的颜色
  @Prop({ default: 24 }) private loadingSize!: number | string; // 加载环的大小
  @Prop({ default: 3 }) private loadingWidth!: number | string; // 加载环的线宽

  @Prop({ default: 'fontColor' }) private loadingIconColor!: string; // 加载图标的颜色
  @Prop({ default: 48 }) private loadingIconSize!: number | string; // 加载图标的大小
  @Prop({ default: 'D3III20glBasic icon-20gl-picture' })
  private loadingIconClass!: string; // 加载图标的名称

  @Prop({ default: false }) private loadingFailed!: boolean; // 加载失败flag
  @Prop({ default: 'fontColor' }) private loadingFailedIconColor!: string; // 加载失败图标的颜色
  @Prop({ default: 44 }) private loadingFailedIconSize!: number | string; // 加载失败图标的大小
  @Prop({ default: 'D3III20glBasic icon-20gl-pictureSplit' })
  private loadingFailedIconClass!: string; // 加载失败图标的名称
}
